	import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
	import {GetUserTkCorrectionList} from 'api/user'
  export default {
	  components:{
	  	UniLoadMore
	  },
    data() {
      return {
		  tabs:['所有记录','未纠正','已纠正'],
        index: 0,
        list: [],
        page: 1,
        pageCount: 1,
		moreStatus:"",
      };
    },
	onPullDownRefresh() {
		this.getList()	
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)	
		}	
	},
    onLoad() {
      this.getList();
    },
    methods: {
    changeTabs(e){
    	if(e.tab === '购买营销卡'){
    		this.getAllList()
    	}else if(e.tab === '我的营销卡'){
    		this.getList()
    	}
    },
      /*滚动加载*/
      onLoad() {
        this.page += 1;
        this.getList(this.page);
      },
	  /**我的营销卡列表*/
	  getList(p=1){
	  	this.page = p
	  	if(this.page==1){
	  		this.list = []
	  	}
	  	this.moreStatus = 'loading'
	  	GetUserTkCorrectionList({
			showstatus:
			  (this.index == 0 && -1) ||
			  (this.index == 1 && 1) ||
			  (this.index == 2 && 0),
	  		page:this.page,
	  		pagesize:10,
	  	}).then(res=>{
	  		uni.stopPullDownRefresh();
	  		this.pageCount = res.pagecount
	  		if(this.page >= this.pageCount){
	  			this.moreStatus = 'noMore'
	  		}else{
	  			this.moreStatus = 'more'
	  		}
	  		if(res.result){
	  			this.list = this.list.concat(res.data)
	  		}
	  	})		
	  }
     
    }
  };
